import React, { Component } from 'react';
import classNames from 'classnames';
import './RewardSetting.scss';

export default class RewardSetting extends Component {
  state = {
    submitted: false,
  };

  onClickRewardSave = () => {
    this.setState({ submitted: true });
  };
  render() {
    const { submitted } = this.state;
    return (
      <div className="reward">
        <h2> </h2>
        <div className="box">
          <div className="text-box">
            <p className={classNames('title', { submitted })}>
              {submitted
                ? ' .'
                : '   .'}
            </p>
            <p className="setting-reward">
               
              {submitted ? (
                <span>100%</span>
              ) : (
                <input type="text" placeholder="100%" />
              )}{' '}
              {' '}
              {submitted ? (
                <span> </span>
              ) : (
                <input
                  type="text"
                  className="long"
                  placeholder=" "
                />
              )}
                 .
              <button onClick={this.onClickRewardSave}></button>
            </p>
          </div>
        </div>
        <div className="progressive-bar">
          <span></span>
          <div className="bar">
            <div className="purpose-wrapper">
              <div style={{ left: '0%' }}>
                <span>0%</span>
                <p>    </p>
                <div />
              </div>
              <div style={{ left: '10%' }}>
                <span>10%</span>
                <p>  </p>
                <div />
              </div>
              <div style={{ left: '100%' }}>
                <span>100%</span>
                <p>    </p>
                <div />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



